import { ethers } from "ethers";
 const RpcUrl = "https://bsc-dataseed1.binance.org/";
export const Zepcoin_Address = "0x0Af7aeE626B2641cb1c91c4D42F903D37D88148F";
export const Ico_Address = "0x0CAB0C21D123CE1c8a35C15Ba32c4789F22a0726";
export const Usdt_Address = "0x55d398326f99059fF775485246999027B3197955";
export const Busd_Address = "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56";
export const providerx = new ethers.providers.JsonRpcProvider(
    RpcUrl,
    );

export const Zepcoin_Abi = require("../contract/Zepcoin.json");
export const Usdt_Abi = require("../contract/usdt-abi.json");
export const Ico_Abi = require("../contract/ico-abi.json");
export const Busd_Abi = require("../contract/Busd.json");

export const Zepcoin_Cn = new ethers.Contract(Zepcoin_Address, Zepcoin_Abi, providerx);
export const Usdt_Cn = new ethers.Contract(Usdt_Address, Usdt_Abi, providerx);
export const Busd_Cn = new ethers.Contract(Busd_Address, Busd_Abi, providerx);
