import { Button, Card, CardContent, Container, Grid, InputAdornment, LinearProgress, MenuItem, OutlinedInput, Select, TextField, Typography } from '@mui/material'
import React, { useRef, useState } from 'react'
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import './HeroStyles.css'
import Coin from '../assest/coin.png'
import Lock from '../assest/lock.png'
import { Link } from 'react-router-dom'
import { ToastError, ToastSuccess } from './toast'
import { ethers } from 'ethers';
import { WalletConnect } from '../blockchain/wallet'
import FlipCountdown from '@rumess/react-flip-countdown';
import Thumbnil from '../assest/thumbnil.png';
import { Ico_Address,Usdt_Address,Busd_Address,Usdt_Abi,Ico_Abi,Busd_Abi,Zepcoin_Cn,Usdt_Cn,Busd_Cn } from '../blockchain/config';
let Dapp_Provider;
let User_Wallet;
const ZepCoinCalculator = () => {
    const curencySelectRef = useRef()
    const [BtnStatus, setBtnStatus] = useState('Buy Now');
    const [Selected_Coin, setSelected_Coin] = useState();
    const [CoinAmount, setCoinAmount] = useState();
    const [referal_id, setreferal_id] = useState(0)
    const [values, setValues] = useState({
        current: 0,
        converted: 0,
        currency: "USD"
    });

    const [SoldOut, setSoldOut] = useState('');
    const [LeftIn, setLeftIn] = useState('');
    const [ico_per, setIcoper] = useState('');
    const [ico_per_str, setIcoperstr] = useState('');
    //console.log(`percenatage sale ${ico_per}   and per str ${ico_per_str} typeof ${Number(ico_per)}`)


    const promise = (async () => {
        try {
            const Ico_Balnce = await new Zepcoin_Cn.balanceOf(Ico_Address);
            // console.log(`Remaining balance is ${String(Ico_Balnce).slice(0,11)}`)
            return BigInt(Ico_Balnce);
        } catch (e) {
            // console.log('error \n' + e.stack);
        }
    })();

    promise.then(val => {
        const actual = 200000000000;
        const LeftIn = String(val).slice(0, 11);
        const SoldOut = actual - Number(LeftIn);
        const ico_per = ((SoldOut / actual) * 100).toFixed(2);

        setSoldOut(SoldOut);
        setLeftIn(LeftIn);
        setIcoperstr(ico_per + '%');
        setIcoper(ico_per)

        // console.log(`sold out ${ico_bal} remaining is  ${ico_remaing}`)
    }).catch(err => {
        // console.log(err);
    });

    const ButtonControler = async () => {
        try {
            if (User_Wallet !== undefined) {
                // console.log(User_Wallet)
                if (Selected_Coin === "USD") {
                    if (CoinAmount === 0) {
                        alert('Coin Amount Not selected');
                    } else {
                        const Usdt_Bal = await Usdt_Cn.balanceOf(User_Wallet);

                        if (CoinAmount >= Usdt_Bal) {
                            alert('insufficient ballance');
                        }
                        else {
                            Approve_Usdt();
                        }
                    }
                }
                if (Selected_Coin === "BUSD") {
                    if (CoinAmount === 0) {
                        alert('Coin Amount Not selected');
                    } else {
                        const Busd_Bal = await Busd_Cn.balanceOf(User_Wallet);
                        if (CoinAmount >= Busd_Bal) {
                            alert('insufficient ballance');
                        }
                        else {
                            Approve_Busd();
                        }
                    }
                }
            } else {
                alert("Please Connect To your Wallet First");
            }

        } catch (error) {
            ToastError("error in payment system")
            console.log(error);
        }



    }

    const Buy_Through_Busd = async () => {
        const Busd_wr = new ethers.Contract(Ico_Address, Ico_Abi, Dapp_Provider);
        const transaction = await Busd_wr.BuyToken_busd(BigInt(CoinAmount));/* global BigInt */
        await transaction.wait();
        alert('Purchase Succesfull');
        setBtnStatus("Buy Now")

    }
    const Approve_Busd = async () => {
        const Busd_wr = new ethers.Contract(Busd_Address, Busd_Abi, Dapp_Provider);
        const transaction = await Busd_wr.approve(Ico_Address, BigInt(CoinAmount));
        setBtnStatus('Please Wait')
        await transaction.wait();
        Buy_Through_Busd()
    }


    const Buy_Through_Usdt = async () => {
        const Usdt_wr = new ethers.Contract(Ico_Address, Ico_Abi, Dapp_Provider);
        const transaction = await Usdt_wr.BuyToken_usdt(BigInt(CoinAmount)); // get data from user
        await transaction.wait();
        ToastSuccess('Purchase Succesfully');
        setBtnStatus("Buy Now")
    }

    const Approve_Usdt = async () => {
        const Busd_wr = new ethers.Contract(Usdt_Address, Usdt_Abi, Dapp_Provider);
        setBtnStatus("Please Wait")
        const transaction = await Busd_wr.approve(Ico_Address, BigInt(CoinAmount));
        await transaction.wait();
        Buy_Through_Usdt()

    }

    const handleConvertValue = event => {
        const { value } = event.target;
        const parsed = parseFloat(value);
        let result = 0

        if (values.currency === 'USD') {
            setSelected_Coin('USD');
            result = parsed * 10000;
            setCoinAmount(parsed * 10 ** 18);
        }
        if (values.currency === 'BUSD') {
            setSelected_Coin('BUSD');
            result = parsed * 10000
            setCoinAmount(parsed * 10 ** 18);
        }
        setValues(pre => ({
            ...pre,
            current: parsed,
            converted: result
        }))
    }
    const [openModal, setOpenModal] = React.useState(false);
    const handleOpenModal = () => setOpenModal(true);
    const handleCloseModal = () => setOpenModal(false);
    const stylemodal = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };

    return (
        <>
            <Card variant='outlined' className='boxdesign'>
                <CardContent>
                    <Typography variant='h3' gutterBottom>
                        ZEPCOIN
                    </Typography>
                    <Typography variant="h6" gutterBottom
                        className='boxtitle'
                    >
                        A TOKEN TO THE ZEPVERSE
                    </Typography>
                </CardContent>
                <CardContent>
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={6}>
                            <Typography variant='subtitle1' style={{ textAlign: "left" }}>$ZEPX Sold</Typography>
                            <TextField value={new Intl.NumberFormat('en-IN').format(SoldOut)} fullWidth variant='filled' disabled
                                className='boxinput'
                                inputProps={{
                                    style: {
                                        background: "white",
                                        padding: "15px",
                                        borderRadius: 10
                                    }
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Typography variant='subtitle1' style={{ textAlign: "left" }}>$ZEPX Left</Typography>
                            <TextField value={new Intl.NumberFormat('en-IN').format(LeftIn)} fullWidth variant='filled' disabled
                                inputProps={{
                                    style: {
                                        color: "black",
                                        background: "white",
                                        padding: "15px",
                                        borderRadius: 10
                                    }
                                }}
                            />
                        </Grid>
                    </Grid>
                </CardContent>
                <CardContent>

                    <LinearProgress value={Number(ico_per)} variant='determinate' color='info' sx={{
                        height: 10,
                        borderRadius: 10
                    }} />
                    <Typography variant='h6'>
                        <div className='icoper'>  {ico_per_str}</div>   <div>Completed</div>
                    </Typography>
                </CardContent>
                <CardContent>
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={6}>
                            <OutlinedInput
                                value={values.current}
                                style={{ backgroundColor: "white", paddingRight: 0 }}
                                onChange={handleConvertValue}
                                type="number"
                                endAdornment={
                                    <InputAdornment position="end">
                                        <Select
                                            autoWidth
                                            ref={curencySelectRef}
                                            value={values.currency}
                                            onChange={event => {
                                                setValues(pre => ({
                                                    ...pre,
                                                    currency: event.target.value
                                                }))
                                            }}
                                        >
                                            <MenuItem value={'USD'}>$USDT</MenuItem>
                                            <MenuItem value={'BUSD'}>$BUSD</MenuItem>
                                        </Select>
                                    </InputAdornment>}
                            />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <OutlinedInput
                                value={values.converted}
                                style={{ backgroundColor: "#fff", color: "#000" }}
                                disabled
                                type="number"
                                endAdornment={
                                    <InputAdornment
                                        position="end">
                                        <Typography variant='body2' color='black'>$ZEPX</Typography>
                                    </InputAdornment>
                                }
                            />
                        </Grid>
                    </Grid>
                </CardContent>
                <CardContent>
                    <Button variant='contained' onClick={ButtonControler} className="btn" style={{
                        width: "auto",
                        padding: "10px 30px",
                        marginRight: "10px"
                    }}>{BtnStatus}</Button>

                    {/* <Button variant='contained' onClick={handleOpenModal} className="btn" style={{
                        width: "auto",
                        padding: "10px 30px"
                    }}>ReferEarn</Button> */}
                    <Modal
                        open={openModal}
                        onClose={handleCloseModal}
                        aria-labelledby="modal-modal-title"
                        aria-describedby="modal-modal-description"
                    >
                        <Box sx={stylemodal}>
                            Hello
                        </Box>
                    </Modal>
                </CardContent>
            </Card>
            <div></div></>
    )
}

const style1 = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 700,
    bgcolor: 'transparent',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};
const Hero = () => {
    const [BtnStatus, setBtnStatus] = useState("CONNECT WALLET")
    const connectWallet = async () => {
        try {
            const obj = await WalletConnect();
            Dapp_Provider = obj.signer;
            User_Wallet = obj.Address;
            setBtnStatus('Connected')
        } catch (error) {

        }
    }



    const [open, setOpen] = React.useState(false);
    const [open1, setOpen1] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [open3, setOpen3] = React.useState(false);

    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const handleOpen1 = () => setOpen1(true);
    const handleClose1 = () => setOpen1(false);
    const handleOpen2 = () => setOpen2(true);
    const handleClose2 = () => setOpen2(false);
    const handleOpen3 = () => setOpen3(true);
    const handleClose3 = () => setOpen3(false);


    const timerSizeValue = () => {
        const { innerWidth } = window;
        if (innerWidth > 960) {
            return 'medium'
        }
        else {
            return 'small'
        }
    }
    return (
        <>

            <header>
                <nav className='navbar'>
                    <div className='logo'>
                        <Link to='/'><img src={Coin} alt='' /></Link>
                    </div>
                    <div className='logotitle'> <p> ZEP COIN (ICO phase 1)</p></div>

                    <div className='logobtn'>
                        <Button onClick={connectWallet} className="btn"><img src={Lock} alt="lock" />{BtnStatus}</Button>
                    </div>
                    {/* <div className='hamburger' onClick={handleClick}>
                {click ? (<FaTimes size={20} style={{ color: '#ffffff' }} />) : (<FaBars size={20} style={{ color: '#ffffff' }} />)}

            </div> */}
                </nav>
            </header>
            <div className='hero'>

                <Container maxWidth='xlg'>

                    <div className='content'>
                        <div className='col-1'>
                            <h3 className='secondarytext bgwhite'>Get Ready To Buy</h3>
                            <h1>ZEP COIN <img src={Coin} alt="coin" /> </h1>
                            <div className='value'><p><span> 1$ =</span> 10,000 ZEPX </p> </div>
                            <div className='timer'>
                                <FlipCountdown
                                    size={timerSizeValue()}
                                    titlePosition='bottom'
                                    hideYear
                                    hideMonth
                                    endAt={'2022-11-22 01:26:58'} // Date/Time
                                />
                            </div>
                            <p> The Future Crypto is here</p>
                        </div>

                        <div className='col-2'>
                            <ZepCoinCalculator />

                            <div className='youtubevedio'>
                                <p>Zepverse Zone:</p>
                                <div className='child1'>
                                    <div className='youtubevedioitem'>
                                        <Button onClick={handleOpen}><img height="80px" src={Thumbnil} alt="vedio" /></Button>
                                        <Modal
                                            open={open}
                                            onClose={handleClose}
                                            aria-labelledby="keep-mounted-modal-title"
                                            aria-describedby="keep-mounted-modal-description"
                                        >
                                            <Box sx={style1}>
                                                <iframe width="660" height="415" src="https://www.youtube.com/embed/D2HnBq8I7EU?controls=0" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"></iframe>
                                            </Box>
                                        </Modal>
                                    </div>
                                    <div className='youtubevedioitem'>
                                        <Button onClick={handleOpen1}><img height="80px" src={Thumbnil} alt="vedio" /></Button>
                                        <Modal
                                            open={open1}
                                            onClose={handleClose1}
                                            aria-labelledby="keep-mounted-modal-title"
                                            aria-describedby="keep-mounted-modal-description"
                                        >
                                            <Box sx={style1}>
                                                <iframe width="560" height="315" src="https://www.youtube.com/embed/tMOuwHcLKxw?controls=0" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"></iframe>
                                            </Box>
                                        </Modal>
                                    </div>
                                </div>
                                <div className='child2'>
                                    <div className='youtubevedioitem'>
                                        <Button onClick={handleOpen2}><img height="80px" src={Thumbnil} alt="vedio" /></Button>
                                        <Modal
                                            keepMounted
                                            open={open2}
                                            onClose={handleClose2}
                                            aria-labelledby="keep-mounted-modal-title"
                                            aria-describedby="keep-mounted-modal-description"
                                        >
                                            <Box sx={style1}>
                                                <iframe width="560" height="315" src="https://www.youtube.com/embed/D2HnBq8I7EU?controls=0" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" ></iframe>
                                            </Box>
                                        </Modal>
                                    </div>
                                    <div className='youtubevedioitem'>
                                        <Button onClick={handleOpen3}><img height="80px" src={Thumbnil} alt="vedio" /></Button>
                                        <Modal
                                            keepMounted
                                            open={open3}
                                            onClose={handleClose3}
                                            aria-labelledby="keep-mounted-modal-title"
                                            aria-describedby="keep-mounted-modal-description"
                                        >
                                            <Box sx={style1}>
                                                <iframe width="560" height="315" src="https://www.youtube.com/embed/D2HnBq8I7EU?controls=0" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"></iframe>

                                            </Box>
                                        </Modal>
                                    </div>


                                </div>
                            </div>

                        </div>

                    </div>
                </Container>

            </div>

        </>
    )
}

export default Hero

